import { Component, ElementRef, EventEmitter, Input, OnInit, Output } from '@angular/core';

export interface Status {
  label: string;
  value: number;
}
@Component({
  selector: 'app-status',
  template: (require('./status.component.html').default || require('./status.component.html')).length ? (require('./status.component.html').default || require('./status.component.html')) : '',
  styles: [(require('./status.component.scss').default || require('./status.component.scss')).length ? (require('./status.component.scss').default || require('./status.component.scss')) : ''],
})
export class StatusComponent implements OnInit {

  @Input() public cols: number
  @Input() public height: number
  @Input() public gutter: number
  @Input() public color: string;
  @Input() public hasLabel: boolean
  @Input() public status: Status;

  @Input() slots: {textRef: ElementRef }

  @Output() click : EventEmitter<void>  = new EventEmitter()

  public steps: number[] = [];

  constructor() { }

  ngOnInit(): void {

    this.cols = this.cols || 8
    this.color = this.color || 'accent';

    for (let i = 1; i <= this.status.value; i++) {
      this.steps.push(100);
    }
    for (let i = [...this.steps].length; i < this.cols; i++) {
      this.steps.push(0);
    }
  }

  public onClick() {
    this.click.emit()
  }
}
